/* Overall page styling */
.HomeSlides {
    font-family: Arial, sans-serif;
    padding: 20px;
  }

  /* Intro Section */
  .Intro {
    text-align: center;
    padding: 50px 20px;
  }

  .Intro h1 {
    font-size: 36px;
    margin-bottom: 20px;
  }

  .Intro p {
    font-size: 18px;
    color: #666;
    max-width: 800px;
    margin: 0 auto;
  }

  /* Service Cards Section */
  .ServiceCards {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 50px 20px;
  }

  .Card {
    background-color: #f5f5f5;
    width: 30%;
    padding: 20px;
    margin: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.3s ease;
  }

  .Card:hover {
    transform: translateY(-10px);
  }

  .Card img {
    max-width: 100px;
    margin-bottom: 20px;
  }

  .Card h3 {
    font-size: 24px;
    margin-bottom: 10px;
  }

  .Card p {
    font-size: 16px;
    color: #555;
  }


  /* Responsive Design */
  @media (max-width: 768px) {
    .ServiceCards {
      flex-direction: column;
      align-items: center;
    }

    .Card {
      width: 100%;
      margin-bottom: 20px;
    }
  }
